import styled from 'styled-components';

const ToggleButton = styled.button`
  	background-color: #ffffff42;
	border: 2px solid gray;
	color: white;
	width: 60px;
	height: 40px;
	font-size: 1em;
	cursor: pointer;
	display: block;

	${ToggleButton}:hover {
		background: #1010100a;
	}
`

export default ToggleButton;


