import styled from 'styled-components';

const CodeEditor = styled.div`
	margin: auto;
	margin-top: 5%;
	margin-bottom: 5%;
	padding: 5%;
	width: 80%;
	height: 200px;
	word-wrap: break-word;
	border: solid gray 3px;
	font-size: 1.5em;
`

export default CodeEditor;


